import { defineComponent, h, inject, reactive, ref, toRefs } from "vue";
import { ElMessage } from "element-plus";
import { useCore, useProxy } from "../../hooks";
import { useApi } from "../form/helper";
import { mergeConfig } from "../../utils";
export default defineComponent({
    name: "cl-upsert",
    emits: ["opened", "closed"],
    props: {
        // 
        items: {
            type: Array,
            default: () => []
        },
        // <el-form /> 
        props: Object,
        // 
        sync: Boolean,
        // 
        op: Object,
        // <cl-dialog /> 
        dialog: Object,
        // 
        onOpen: Function,
        // 
        onOpened: Function,
        // 
        onClose: Function,
        // 
        onClosed: Function,
        // 
        onInfo: Function,
        // 
        onSubmit: Function
    },
    setup(props, { slots, expose }) {
        const { crud } = useCore();
        const config = reactive(mergeConfig(props, inject("useUpsert__options") || {}));
        // el-form
        const Form = ref();
        // 
        const mode = ref("info");
        // 
        function close(action) {
            Form.value?.close(action);
        }
        // 
        function onClosed() {
            Form.value?.hideLoading();
            if (config.onClosed) {
                config.onClosed();
            }
        }
        // 
        function beforeClose(action, done) {
            function next() {
                done();
                onClosed();
            }
            if (config.onClose) {
                config.onClose(action, next);
            }
            else {
                next();
            }
        }
        // 
        function submit(data) {
            const { service, dict, refresh } = crud;
            function done() {
                Form.value?.done();
            }
            function next(data) {
                return new Promise((resolve, reject) => {
                    // 
                    service[dict.api[mode.value]](data)
                        .then((res) => {
                        ElMessage.success(dict.label.saveSuccess);
                        done();
                        close("save");
                        refresh();
                        resolve(res);
                    })
                        .catch((err) => {
                        ElMessage.error(err.message);
                        done();
                        reject(err);
                    });
                });
            }
            // 
            if (config.onSubmit) {
                config.onSubmit(data, {
                    done,
                    next,
                    close() {
                        close("save");
                    }
                });
            }
            else {
                next(data);
            }
        }
        // 
        function open() {
            // 
            const isDisabled = mode.value == "info";
            return new Promise((resolve) => {
                if (!Form.value) {
                    return console.error("<cl-upsert /> is not found");
                }
                Form.value?.open({
                    title: crud.dict.label[mode.value],
                    props: {
                        ...config.props,
                        disabled: isDisabled
                    },
                    op: {
                        ...config.op,
                        hidden: isDisabled
                    },
                    dialog: config.dialog,
                    items: config.items || [],
                    on: {
                        open(data) {
                            if (config.onOpen) {
                                config.onOpen(data);
                            }
                            resolve(true);
                        },
                        submit,
                        close: beforeClose
                    },
                    form: {},
                    _data: {
                        isDisabled
                    }
                }, config.plugins);
            });
        }
        // 
        function onOpened() {
            const data = Form.value?.getForm();
            if (config.onOpened) {
                config.onOpened(data);
            }
        }
        // 
        async function add() {
            mode.value = "add";
            // 
            await open();
            // 
            onOpened();
        }
        // 
        async function append(data) {
            mode.value = "add";
            // 
            await open();
            // 
            if (data) {
                Form.value?.bindForm(data);
            }
            // 
            onOpened();
        }
        // 
        function edit(data) {
            mode.value = "update";
            getInfo(data);
        }
        // 
        function info(data) {
            mode.value = "info";
            getInfo(data);
        }
        // 
        function getInfo(data) {
            // 
            Form.value?.showLoading();
            // 
            if (!config.sync) {
                open();
            }
            // 
            async function done(data) {
                // 
                Form.value?.hideLoading();
                // 
                if (data) {
                    Form.value?.bindForm(data);
                }
                // 
                if (config.sync) {
                    await open();
                }
                onOpened();
            }
            // 
            function next(data) {
                return new Promise(async (resolve, reject) => {
                    // 
                    await crud.service[crud.dict.api.info]({
                        [crud.dict.primaryId]: data[crud.dict.primaryId]
                    })
                        .then((res) => {
                        done(res);
                        resolve(res);
                    })
                        .catch((err) => {
                        ElMessage.error(err.message);
                        reject(err);
                    });
                    // 
                    Form.value?.hideLoading();
                });
            }
            // 
            if (config.onInfo) {
                config.onInfo(data, {
                    close,
                    next,
                    done
                });
            }
            else {
                next(data);
            }
        }
        // 
        function done() {
            Form.value?.hideLoading();
        }
        const ctx = {
            config,
            ...toRefs(config),
            ...useApi({ Form }),
            Form,
            get form() {
                return Form.value?.form || {};
            },
            mode,
            add,
            append,
            edit,
            info,
            open,
            close,
            done,
            submit
        };
        useProxy(ctx);
        expose(ctx);
        return () => {
            return (<div class="cl-upsert">
					{h(<cl-form ref={Form} custom-class={`is-${mode.value}`}/>, {}, slots)}
				</div>);
        };
    }
});
