import { defineComponent, getCurrentInstance, inject, provide, reactive } from "vue";
import { merge } from "merge";
import { useHelper } from "./helper";
import Mitt from "../../utils/mitt";
import { cloneDeep, deepMerge, mergeConfig } from "../../utils";
import { crudList } from "../../emitter";
import { useGlobal } from "../../hooks";
export default defineComponent({
    name: "cl-crud",
    props: {
        // 
        name: String,
        // 
        border: Boolean,
        // 
        padding: {
            type: String,
            default: "10px"
        }
    },
    setup(props, { slots, expose }) {
        const { uid } = getCurrentInstance();
        // 
        const config = reactive(mergeConfig(inject("useCrud__options") || {}));
        // 
        const mitt = new Mitt(uid);
        // 
        const { dict, permission } = useGlobal();
        // 
        const crud = reactive(deepMerge({
            id: props.name || uid,
            // 
            routePath: location.pathname || "/",
            // 
            table: {
                contextMenu: true,
                border: true
            },
            // 
            loading: false,
            // 
            selection: [],
            // 
            params: {
                page: 1,
                size: 20
            },
            // 
            service: {},
            // 
            dict: {},
            // 
            permission: {}
        }, cloneDeep({ dict, permission })));
        // 
        crudList.push(merge(crud, useHelper({ mitt, config, crud })));
        // 
        provide("crud", crud);
        provide("mitt", mitt);
        // 
        expose(crud);
        return () => {
            return (<div class={["cl-crud", { "is-border": props.border }]} style={{ padding: props.padding }}>
					{slots.default && slots.default()}
				</div>);
        };
    }
});
